@font-face {
  font-family: 'Avenir Next Regular';
  font-style: normal;
  font-weight: 400;
  src: 
      url('./assets/fonts/AvenirNextLTPro-Regular.woff') format('woff'),
      url('./assets/fonts/AvenirNextLTPro-Regular.woff2') format('woff2')
}
@font-face {
  font-family: 'Avenir Next Demi Bold';
  font-style: bold;
  font-weight: 600;
  src: 
        url('./assets/fonts/AvenirNext-DemiBold.ttf') format('truetype');
}
* {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}
.App {
  text-align: center;
  position: absolute;
  top: 75px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-line-pack: stretch;
      align-content: stretch;
  -ms-flex-align: stretch;
      align-items: stretch;
  
  width: 100vw;
}
.flex-row {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
}
.flex-column {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
}
.itemName {
  font-size: 16px;
  font-weight: bold;
}
.nameFont{
  font-family: 'Avenir Next Demi Bold'
}
.catFont {
  font-family: 'Avenir Next Regular';
}
.detailFont {
  font-family: 'Avenir Next Regular';
  font-size: 16px;
}
.itemCat {
  font-size: 12px;
  font-weight: normal;
}
.listItemTextContainer{
